.Rectangle-2 {
    width: 750px;
    height: 646px;
    margin: 30px 345px 165px;
    padding: 29px 28px 27px;
    border-radius: 40px;
    box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.25),
        inset 1px 1px 0 0 rgba(111, 110, 143, 0.3);
    border-style: solid;
    border-width: 1px;
    border-image-source: linear-gradient(
        132deg,
        rgba(255, 255, 255, 0.27) -3%,
        rgba(255, 255, 255, 0) 51%,
        rgba(255, 255, 255, 0.54) 105%
    );
    border-image-slice: 1;
    background-image: linear-gradient(to bottom, #020211, #0c0b2f),
        linear-gradient(
            132deg,
            rgba(255, 255, 255, 0.27) -3%,
            rgba(255, 255, 255, 0) 51%,
            rgba(255, 255, 255, 0.54) 105%
        );
    background-origin: border-box;
    background-clip: content-box, border-box;
}

